<template>
	<div>
    <div class="block">
      <div>
        <el-table
          ref="filterTable"
          :data="tableData"
          @filter-change="filterStatus"
          stripe
          style="width: 100%"
          height="750">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label=": ">
                  <span>{{ props.row.from_name }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.from_tel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.from_addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.to_name }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.to_tel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.to_addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.org }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.dt }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column
            type="index"
            :index="indexMethod"
            width="50">
          </el-table-column>
          <el-table-column
            type="selection"
            width="70">
          </el-table-column>
          <el-table-column
            label=""
            prop="to_name"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="id"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="org"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="status"
            width="150"
            :filters="[{ text: '', value: '' }, { text: '', value: '' }
              , { text: '', value: '' }, { text: '', value: '' }]"
            column-key="status"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.status === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.status }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="dt"
            width="250">
          </el-table-column>
          <el-table-column
            align="right"
            width="300">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""
                @keyup.enter.native="searchHandler"/>
            </template>
          </el-table-column>
          <el-table-column>
            <template slot="header" slot-scope="scope">
              <!--  -->
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="medium"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="confirmSelection"></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="medium"
                  type="success"
                  icon="el-icon-check"
                  circle
                  @click="outSelection"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          :hide-on-single-page="value"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "AdminCollection",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        currentPage: 1,       // 
        total: 21,            // 
        pageSize: 10,         // 
        search: '',
        tableData: [{
          from_name: '',
          from_tel: '13305193691',
          from_addr: '',
          to_name: '',
          to_tel: '17751773079',
          to_addr: '',
          id: '75422907315890',
          org: '',
          status: '',
          dt: '2021-01-05 16:45:00',
        }],
        statusFilter: ''
      }
    },
    methods: {
		  // 
      handleSizeChange(val) {
        console.log(` ${val} `)
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`)
        this.getPacks(this.statusFilter)
      },
      // 
      handleConfirm(ids, count) {
        const _this = this
        if (count > 0) {
          this.$message({
            showClose: true,
            message: '' + count + '!',
            type: 'warning'
          })
        }
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = new URLSearchParams()
          let token = sessionStorage.getItem("token")
          console.log('ids: ' + ids)
          param.append('ids', ids)
          param.append('token', token)
          _this.$axios({
            method: 'put',
            url: _this.baseUrl + '/send/confirm',
            data: param
          })
            .then(function (response) {
              console.log(response.data)
              if (response.data === 'please login to operate') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
                _this.$router.push('/loginAndRegister')
              } else if (response.data === 'do fail') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
              } else if (response.data === 'do success') {
                _this.$message({
                  showClose: true,
                  type: 'success',
                  message: '!'
                })
                let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                _this.$router.push(NewPage)
                _this.$router.go(-1)
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$notify.error({
                showClose: true,
                title: '',
                message: ''
              })
            })
        }).catch(() => {
          _this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
      },
      // 
      handleOut(ids, outCount, payCount) {
        const _this = this
        if (outCount > 0) {
          this.$message({
            showClose: true,
            message: '' + outCount + '!',
            type: 'warning'
          })
        }
        if (payCount > 0) {
          this.$message({
            showClose: true,
            message: '' + payCount + '!',
            type: 'warning'
          })
        }
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = new URLSearchParams()
          let token = sessionStorage.getItem("token")
          console.log('ids: ' + ids)
          param.append('ids', ids)
          param.append('token', token)
          _this.$axios({
            method: 'put',
            url: _this.baseUrl + '/send/out',
            data: param
          })
            .then(function (response) {
              console.log(response.data)
              if (response.data === 'please login to operate') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
                _this.$router.push('/loginAndRegister')
              } else if (response.data === 'do fail') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
              } else if (response.data === 'do success') {
                _this.$message({
                  showClose: true,
                  type: 'success',
                  message: '!'
                })
                let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                _this.$router.push(NewPage)
                _this.$router.go(-1)
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$notify.error({
                showClose: true,
                title: '',
                message: ''
              })
            })
        }).catch(() => {
          _this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
      },
      // 
      searchHandler() {
        this.$message({
          showClose: true,
          type: 'info',
          message: ''
        })
      },
      // 
      filterStatus(filters) {
        if (filters.status !== undefined) {
          this.statusFilter = filters.status
          console.log("status: " + this.statusFilter)
        }
        this.getPacks(this.statusFilter)
      },
      // 
      getPacks(status) {
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        let jsonParam = {
          "currentPage" : this.currentPage,
          "pageSize" : this.pageSize,
          "token" : token,
          "status" : status
        }
        param.append('json', JSON.stringify(jsonParam))
        const _this = this
        console.log("")
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/send/getSendByAdmin',
          data: param
        })
          .then(function (response) {
            console.log("")
            console.log(response.data)
            if (response.data.fail === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
              _this.$router.push('/loginAndRegister')
            } else {
              _this.total = response.data.result.total
              _this.tableData = response.data.result.list
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      indexMethod(index) {
        return (this.currentPage - 1) * this.pageSize + index + 1
      },
      // 
      confirmSelection() {
        const _this = this
        if (this.$refs.filterTable.selection.length === 0) {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        } else {
          let ids = ''
          let count = 0
          for (let i = 0; i < _this.$refs.filterTable.selection.length; i++) {
            if (_this.$refs.filterTable.selection[i].status === '') {
              ids += _this.$refs.filterTable.selection[i].id + ','
            } else {
              count++
            }
          }
          this.handleConfirm(ids, count)
        }
      },
      // 
      outSelection() {
        const _this = this
        if (this.$refs.filterTable.selection.length === 0) {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        } else {
          let ids = ''
          let outCount = 0
          let payCount = 0
          for (let i = 0; i < _this.$refs.filterTable.selection.length; i++) {
            if (_this.$refs.filterTable.selection[i].status === '') {
              outCount++
              continue
            }
            if (_this.$refs.filterTable.selection[i].status === '' || _this.$refs.filterTable.selection[i].status === '') {
              payCount++
              continue
            }
            ids += _this.$refs.filterTable.selection[i].id + ','
          }
          this.handleOut(ids, outCount, payCount)
        }
      }
    },
    created() {
		  this.getPacks("")
    },
    mounted() {

    },
    updated() {

    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
    font-weight: bold;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



