<template>
	<div>
    <el-row><br></el-row>
    <el-row><br></el-row>
    <el-row>
      <el-button type="text" @click="dialogFormVisible = true"></el-button>
    </el-row>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="pick">
        <el-row>
          <el-col :span="10" :offset="6">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="pick.addr" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10" :offset="6">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="pick.code" autocomplete="off" placeholder="" style="width: 220px"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="pickPack"> </el-button>
      </div>
    </el-dialog>

    <div>
      <el-row>
        <el-badge :value="allTotal" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="isTotal" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="noTotal" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendSubmit" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendConfirm" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendPay" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendOut" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "UserAside",
    data() {
		  return {
		    baseUrl: Constant.data.baseUrl,
        dialogFormVisible: false,
        pick: {
          addr: "",
          code: ""
        },
        formLabelWidth: '120px',
        // 
        allTotal: 100,
        // 
        isTotal: 20,
        // 
        noTotal: 80,
        // 
        sendSubmit: 4,
        // 
        sendConfirm: 3,
        // 
        sendPay: 2,
        // 
        sendOut: 1,
      }
    },
    methods: {
		  // 
		  pickPack() {
        let _this = this
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        param.append('addr', this.pick.addr)
        param.append('code', this.pick.code)
        param.append('token', token)
        this.$axios({
          method: 'put',
          url: _this.baseUrl + '/pack/pickPackByUser',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data === 'pick up the package success') {
              _this.$message({
                showClose: true,
                message: '',
                type: 'success'
              })
              let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
              _this.$router.push(NewPage)
              _this.$router.go(-1)
            } else if (response.data === 'take over') {
              _this.$message({
                showClose: true,
                message: '!',
                type: 'success'
              })
              let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
              _this.$router.push(NewPage)
              _this.$router.go(-1)
            } else if (response.data === 'not exist') {
              _this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              })
            } else if (response.data === 'please login to operate') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '!',
                type: 'warning'
              })
              _this.$router.push('/LoginAndRegister')
            } else {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
        this.dialogFormVisible = false
        this.pick.addr = ""
        this.pick.code = ""
      },
      // 
      getTotal() {
        let _this = this
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        param.append("token", token)
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/pack/getUserTotalNum',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.result === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            } else {
              _this.allTotal = response.data.allTotal
              _this.isTotal = response.data.isTotal
              _this.noTotal = response.data.noTotal
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/send/getTotalByUser',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.result === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            } else {
              _this.sendSubmit = response.data.sendSubmit
              _this.sendConfirm = response.data.sendConfirm
              _this.sendPay = response.data.sendPay
              _this.sendOut = response.data.sendOut
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
    },
    mounted() {
		  this.getTotal()
    }
  }
</script>

<style scoped>
  .item {
    margin-top: 10px;
    margin-right: 40px;
  }
</style>



