<template>
  <div>
    <el-row><br></el-row>
    <el-row><br></el-row>
    <el-row>
      <el-button type="text" @click="addPack"></el-button>
    </el-row>

    <div>
      <!-- :notTotal / maxTotal -->
      <el-row>
        <el-badge :value="allTotal" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="isTotal" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="noTotal" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <div class="bottom">
        <div class="block">
          <el-tooltip class="item" effect="dark" content="" placement="right">
            <el-progress type="circle" :percentage="percentage" :color="colors" stroke-width="12"></el-progress>
          </el-tooltip>
        </div>
      </div>
      <el-row>
        <el-badge :value="sendSubmit" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendConfirm" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendPay" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
      <el-row>
        <el-badge :value="sendOut" class="item">
          <el-button size="small"></el-button>
        </el-badge>
      </el-row>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "AdminAside",
    data() {
		  return {
		    baseUrl: Constant.data.baseUrl,
        // 
        allTotal: 100,
        // 
        isTotal: 20,
        // 
        notTotal: 80,
        // 
        sendTotal: 0,
        // 
        maxTotal: 2400,
        // 
        percentage: 20,
        colors: [
          {color: '#1989fa', percentage: 30},
          {color: '#5cb87a', percentage: 60},
          {color: '#ffd530', percentage: 80},
          {color: '#ff8839', percentage: 90},
          {color: '#ee2525', percentage: 100},
        ],
        // 
        sendSubmit: 4,
        // 
        sendConfirm: 3,
        // 
        sendPay: 2,
        // 
        sendOut: 1,
      }
    },
    methods: {
		  // 
      getTotal() {
        let _this = this
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        param.append("token", token)
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/pack/getAdminTotalNum',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.result === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            } else {
              // 
              _this.percentage = response.data.percentage.toFixed(2)
              _this.allTotal = response.data.allTotal
              _this.isTotal = response.data.isTotal
              _this.noTotal = response.data.noTotal
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/send/getTotalByAdmin',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.result === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            } else {
              _this.sendSubmit = response.data.sendSubmit
              _this.sendConfirm = response.data.sendConfirm
              _this.sendPay = response.data.sendPay
              _this.sendOut = response.data.sendOut
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      addPack() {
        let _this = this
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({ value }) => {
          let param = new URLSearchParams()
          let token = sessionStorage.getItem("token")
          param.append('id', value)
          param.append('token', token)
          _this.$axios({
            method: 'post',
            url: _this.baseUrl + '/pack/addPack',
            data: param
          })
            .then(function (response) {
              console.log(response.data)
              if (response.data === 'the package enter addr success') {
                _this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
                let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                _this.$router.push(NewPage)
                _this.$router.go(-1)
              } else if (response.data === 'please login to operate') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
                _this.$router.push('/LoginAndRegister')
              } else {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$notify.error({
                showClose: true,
                title: '',
                message: ''
              })
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    mounted() {
		  this.getTotal()
    }
  }
</script>

<style scoped>
  .item {
    margin-top: 10px;
    margin-right: 40px;
  }

  .right {
    float: right;
    width: 60px;
  }

  .bottom {
    clear: both;
    text-align: center;
  }
</style>



