<template>
	<div>
    <el-row>
      <el-col :span="8" :offset="8">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="">
            <el-input v-model="form.card" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.addr" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.count" disabled></el-input>
          </el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "AdminInfo",
    data() {
		  return {
		    baseUrl: Constant.data.baseUrl,
        form: {
          card: '2101',
          phone: '17700000000',
          name: '',
          addr: '',
          count: 0
        }
      }
    },
    methods: {
		  // 
		  getAdminInfo() {
        const _this = this
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        param.append("token", token)
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/admin/getInfo',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.result === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            } else {
              _this.form.card = response.data.admin.card
              _this.form.phone = response.data.admin.phone
              _this.form.name = response.data.admin.name
              _this.form.addr = response.data.admin.addr
              _this.form.count = response.data.admin.count
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      onSubmit() {
        const _this = this
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        param.append("token", token)
        param.append("name", this.form.name)
        param.append("phone", this.form.phone)
        this.$axios({
          method: 'put',
          url: _this.baseUrl + '/admin/updateInfo',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.result === 'do success') {
              _this.$message({
                showClose: true,
                message: '',
                type: 'success'
              })
              _this.getAdminInfo()
            } else if (response.data.result === 'please login to operate') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '!',
                type: 'warning'
              })
              _this.$router.push('/LoginAndRegister')
            } else {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      }
    },
    created() {
		  this.getAdminInfo()
    }
  }
</script>

<style scoped>

</style>



