<template>
	<div>
    <el-row>
      <el-col :span="8" class="grid-content">
        <div id="title">  Web         </div>
      </el-col>
      <el-col :span="8" class="grid-content">
        <el-menu
          :default-active="$route.path"
          class="el-menu-demo"
          router
          mode="horizontal"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b">
          <el-menu-item>
            <div class="bottom">
              <div class="block">
                <el-tooltip class="item" effect="dark" :content="name" placement="bottom">
                  <el-avatar :size="40" :src="imgUrl"></el-avatar>
                </el-tooltip>
              </div>
            </div>
          </el-menu-item>
          <el-menu-item index="/userHome/send" @click.native="removePic"></el-menu-item>
          <el-submenu index="1">
            <template slot="title"></template>
            <el-menu-item index="/userHome/allPacks" @click.native="removePic"></el-menu-item>
            <el-menu-item index="/userHome/isPacks" @click.native="removePic"></el-menu-item>
            <el-menu-item index="/userHome/noPacks" @click.native="removePic"></el-menu-item>
            <el-menu-item index="/userHome/sendList" @click.native="removePic"></el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"></template>
            <el-menu-item index="/userHome/info" @click.native="removePic"></el-menu-item>
            <el-menu-item index="/userHome/resetPwd" @click.native=""></el-menu-item>
          </el-submenu>
          <el-menu-item>
            <el-popconfirm title="" @confirm="logout">
              <el-button type="text" slot="reference"></el-button>
            </el-popconfirm>
          </el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "UserHeader",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        imgUrl: '',
        card: '',
        name: ''
      };
    },
    methods: {
		  // 
      logout() {
        const _this = this
        let token = sessionStorage.getItem("token")
        let param = new URLSearchParams()
        param.append('token', token)
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/user/logout',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data === 'exit success') {
              sessionStorage.removeItem("token")
              sessionStorage.removeItem("card")
              sessionStorage.removeItem("userCard")
              sessionStorage.removeItem("name")
              _this.$message({
                showClose: true,
                message: ''
              })
              sessionStorage.removeItem("codePic")
              _this.$router.push('/')
            } else {
              console.log("")
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      getUserInfo() {
        this.card = sessionStorage.getItem("card")
        this.name = sessionStorage.getItem("name")
      }
      // removePic() {
      //   sessionStorage.removeItem("codePic")
      // }
    },
    created() {
		  let userUrl = 'assets/image/user.png'
      this.imgUrl = require("@/" + userUrl)
      this.getUserInfo()
    },
    mounted() {
      this.getUserInfo()
    }
  }
</script>

<style scoped>
  .grid-content {
    border-radius: 4px;
    min-height: 40px;
  }

  .bottom {
    clear: both;
    text-align: center;
  }

  .item {
    margin: 4px;
  }

  #title {
    font: 20px Extra large;
    font-family: "";
    line-height: 1.7;
    text-align: center;
    color: coral;
    margin-top: 3%;
    /*position: absolute;*/
    left: 20%;
  }

  .el-menu-demo {
    position: absolute;
    right: 1%;
  }
</style>



