<template>
  <div>
    <el-row>
      <el-col :span="8" :offset="8">
        <el-form ref="form" :model="form" label-width="150px" :rules="rules" status-icon>
          <el-form-item label="" prop="admin" :required="true">
            <el-select v-model="form.admin" placeholder="" clearable>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="name" :required="true">
            <el-input v-model="form.name" clearable placeholder="" style="width: 220px"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone" :required="true">
            <el-input v-model="form.phone" clearable placeholder="" style="width: 220px"></el-input>
          </el-form-item>
          <el-form-item label="" prop="addr" :required="true">
            <div class="block">
              <el-cascader
                v-model="form.addr"
                clearable
                filterable
                placeholder=""
                :options="region"
                :props="{ expandTrigger: 'hover' }"
                @change="handleChangeCity"></el-cascader>
            </div>
          </el-form-item>

          <el-form-item label="" prop="info" :required="true">
            <el-select v-model="form.info" placeholder="" clearable>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-link type="primary" :underline="false" @click="prohibitDrawer = true"></el-link>

          <el-form-item label="" prop="weight" :required="true">
            <el-input-number v-model="form.weight" @change="handleChangeWeight" :min="1" :max="100" label=""></el-input-number>
          </el-form-item>
          <el-link type="primary" :underline="false" @click="priceTable = true"></el-link>

          <el-form-item label="">
            <el-switch
              v-model="form.hasPack"
              active-text=""
              inactive-text="">
            </el-switch>
          </el-form-item>

          <el-form-item label="">
            <el-checkbox v-model="form.isRead">
              
              <el-link type="primary" :underline="false" href="#">
                
              </el-link>
            </el-checkbox>
          </el-form-item>

          <el-form-item label="">
            {{ form.price }}
            <el-link type="primary" :underline="false" @click="clickPrice">
              
            </el-link>
          </el-form-item>
          <el-button type="primary" @click="onSubmit('form')" :disabled="!form.isRead"></el-button>
        </el-form>
      </el-col>
    </el-row>

    <el-drawer
      :title=priceDataTitle
      :visible.sync="priceTable"
      direction="rtl"
      size="50%">
      <el-table :data="priceData">
        <el-table-column property="province" label="" width="200"></el-table-column>
        <el-table-column property="code" label="" width="200"></el-table-column>
        <el-table-column property="base" label="" width="200"></el-table-column>
        <el-table-column property="continue" label="" width="200"></el-table-column>
      </el-table>
    </el-drawer>

    <el-drawer
      :title=prohibitDrawerTitle
      :visible.sync="prohibitDrawer"
      direction="rtl"
      size="50%"
      style="text-align: left;font-family: ;font-size: 20px">
      <h1></h1><br>
      <b>1. </b><br>
      <b>2. </b><br>
      <h1></h1><br>
      <b>1. </b><br>
      <b>2. </b><br>
      <h1></h1><br>
      <b>1. </b><br>
      <b>2. </b><br>
      <b>3. </b><br>
      <h1></h1><br>
      <b>1. </b><br>' +
      <b>2. </b><br>
      <b>3. </b><br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <b>1. </b><br>
      <b>2. </b><br>
      <b>3. </b><br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <b>1. </b><br>
      <b>2. </b><br>
      <b>3. </b><br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <b>1. </b><br>
      <b>2. </b><br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      <br>
      <h1></h1><br>
      '
    </el-drawer>
  </div>
</template>

<script>
  import { regionData, CodeToText } from 'element-china-area-data'
  import Constant from "../pub/Constant"
	export default {
		name: "UserSend",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        form: {
          admin: '',
          name: '',
          phone: '',
          addr: [],
          info: '',
          weight: 1,
          hasPack: false,
          isRead: false,
          price: 0
        },
        region: regionData,
        rules: {
          admin: [
            { required: true, message: '!', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '!', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '!', trigger: 'blur' },
            { min: 8, max: 11, message: '!', trigger: 'blur' }
          ],
          addr: [
            { required: true, message: '!', trigger: 'blur' }
          ],
          info: [
            { required: true, message: '!', trigger: 'blur' }
          ]
        },
        price: {
          base: 0,
          continue: 0
        },
        priceTable: false,
        prohibitDrawer: false,
        priceData: [
          { province: '', code: '310000', base: 5, continue: 1},
          { province: '', code: '320000', base: 5, continue: 1},
          { province: '', code: '330000', base: 5, continue: 1},
          { province: '', code: '340000', base: 10, continue: 8},
          { province: '', code: '110000', base: 13, continue: 10},
          { province: '', code: '120000', base: 13, continue: 10},
          { province: '', code: '130000', base: 13, continue: 10},
          { province: '', code: '140000', base: 13, continue: 10},
          { province: '', code: '220000', base: 13, continue: 10},
          { province: '', code: '350000', base: 13, continue: 10},
          { province: '', code: '360000', base: 13, continue: 10},
          { province: '', code: '370000', base: 13, continue: 10},
          { province: '', code: '410000', base: 13, continue: 10},
          { province: '', code: '420000', base: 13, continue: 10},
          { province: '', code: '430000', base: 13, continue: 10},
          { province: '', code: '440000', base: 13, continue: 10},
          { province: '', code: '500000', base: 13, continue: 10},
          { province: '', code: '510000', base: 13, continue: 10},
          { province: '', code: '520000', base: 13, continue: 10},
          { province: '', code: '530000', base: 13, continue: 10},
          { province: '', code: '150000', base: 15, continue: 10},
          { province: '', code: '210000', base: 15, continue: 10},
          { province: '', code: '230000', base: 15, continue: 10},
          { province: '', code: '450000', base: 15, continue: 10},
          { province: '', code: '460000', base: 15, continue: 10},
          { province: '', code: '610000', base: 15, continue: 10},
          { province: '', code: '620000', base: 15, continue: 10},
          { province: '', code: '630000', base: 15, continue: 10},
          { province: '', code: '640000', base: 15, continue: 10},
          { province: '', code: '650000', base: 15, continue: 10},
          { province: '', code: '540000', base: 0, continue: 0},
          { province: '', code: '710000', base: 0, continue: 0},
          { province: '', code: '810000', base: 0, continue: 0},
          { province: '', code: '820000', base: 0, continue: 0}
        ],
        priceDataTitle: ' airyland/china-area-data',
        prohibitDrawerTitle: ''
      }
    },
    methods: {
		  // 
      onSubmit(formName) {
        const _this = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (_this.form.addr[0] === '540000' || _this.form.addr[0] === '710000' ||
              _this.form.addr[0] === '810000' || _this.form.addr[0] === '820000' ||
              _this.form.addr[0] === '') {
              _this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              })
            } else {
              let token = sessionStorage.getItem('token')
              let addr = CodeToText[_this.form.addr[0]] + CodeToText[_this.form.addr[1]] + CodeToText[_this.form.addr[2]]
              _this.$axios({
                method: 'post',
                url: _this.baseUrl + '/send/getSendInfo',
                data: {
                  admin: _this.form.admin,
                  name: _this.form.name,
                  phone: _this.form.phone,
                  addr: addr,
                  info: _this.form.info,
                  weight: _this.form.weight,
                  price: _this.form.price,
                  token: token
                }
              })
                .then(function (response) {
                  console.log(response.data)
                  if (response.data.result === 'do success') {
                    _this.$message({
                      showClose: true,
                      message: '',
                      type: 'success'
                    })
                    let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                    _this.$router.push(NewPage)
                    _this.$router.go(-1)
                  } else if (response.data.result === 'do fail') {
                    _this.$notify({
                      showClose: true,
                      title: '',
                      message: '',
                      type: 'warning'
                    })
                  } else if (response.data.result === 'please login to operate') {
                    _this.$notify({
                      showClose: true,
                      title: '',
                      message: '',
                      type: 'warning'
                    })
                    _this.$router.push('/LoginAndRegister')
                  }
                })
                .catch(function (error) {
                  console.log(error)
                  _this.$notify.error({
                    showClose: true,
                    title: '',
                    message: ''
                  })
                })
            }
          } else {
            console.log('error submit!!')
            _this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            })
            return false
          }
        })
      },
      // 
      handleChangeWeight(value) {
        console.log(value)
      },
      // 
      handleChangeCity(value) {
        console.log(CodeToText[value[0]])       // 
        console.log(CodeToText[value[1]])       // 
        console.log(CodeToText[value[2]])       // 
      },
      // 
      setPrice() {
        const _this = this
        if (_this.form.addr[0] === '310000' || _this.form.addr[0] === '320000' || _this.form.addr[0] === '330000') {
          _this.form.price = 4 + _this.form.weight
          _this.price.base = 5
          _this.price.weight = 1
        } else if (_this.form.addr[0] === '110000' || _this.form.addr[0] === '120000' || _this.form.addr[0] === '130000'
          || _this.form.addr[0] === '140000' || _this.form.addr[0] === '220000' || _this.form.addr[0] === '350000'
          || _this.form.addr[0] === '360000' || _this.form.addr[0] === '370000' || _this.form.addr[0] === '410000'
          || _this.form.addr[0] === '420000' || _this.form.addr[0] === '430000' || _this.form.addr[0] === '440000'
          || _this.form.addr[0] === '500000' || _this.form.addr[0] === '410000' || _this.form.addr[0] === '510000'
          || _this.form.addr[0] === '520000' || _this.form.addr[0] === '530000') {
          _this.form.price = 3 + 10 * _this.form.weight
          _this.price.base = 13
          _this.price.weight = 10
        } else if (_this.form.addr[0] === '540000' || _this.form.addr[0] === '710000' || _this.form.addr[0] === '810000' ||
          _this.form.addr[0] === '820000') {
          _this.form.price = 0
          _this.price.base = 0
          _this.price.weight = 0
        } else if (_this.form.addr[0] === '340000') {
          _this.form.price = 2 + 8 * _this.form.weight
          _this.price.base = 10
          _this.price.weight = 8
        } else if (_this.form.addr[0] === '150000' || _this.form.addr[0] === '210000' || _this.form.addr[0] === '230000'
          || _this.form.addr[0] === '450000' || _this.form.addr[0] === '460000' || _this.form.addr[0] === '610000'
          || _this.form.addr[0] === '620000' || _this.form.addr[0] === '630000' || _this.form.addr[0] === '640000'
          || _this.form.addr[0] === '650000') {
          _this.form.price = 5 + 10 * _this.form.weight
          _this.price.base = 15
          _this.price.weight = 10
        }
      },
      // 
      clickPrice() {
        const _this = this
        this.$msgbox({
          title: '',
          message: '<strong></strong>' + _this.price.base
            + '<br><strong></strong>' + _this.price.continue
            + '/kg<br><strong></strong>' + _this.form.price,
          dangerouslyUseHTMLString: true,
          center: true
        })
      },
		},
    updated() {
		  this.setPrice()
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss">

  /* el-drawer  */
  .el-drawer.rtl {
    overflow: scroll;
  }

  /*.el-input__inner {*/
  /*  width: 300px;*/
  /*}*/
</style>



