<template>
	<div>
    <div class="block">
      <div>
        <el-table
          ref="filterTable"
          :data="tableData"
          @filter-change="handleFilter"
          stripe
          style="width: 100%"
          height="750">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label=": ">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.org }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perAddr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.code }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.start }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.end }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.pick }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column
            type="index"
            :index="indexMethod"
            width="100">
          </el-table-column>
          <el-table-column
            label=""
            prop="id"
            width="250">
          </el-table-column>
          <el-table-column
            label=""
            prop="org"
            width="150"
            :filters="filters"
            column-key="org"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.org === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.org }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="perName"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="perTel"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="code"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="status"
            width="150"
            :filters="[{ text: '', value: 0 }, { text: '', value: 1 }, { text: '', value: -1 }]"
            column-key="status"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.status === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.status }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column width="50">
            <template slot="header" slot-scope="scope">
              <!--  -->
              <el-tooltip class="item" effect="dark" content=" Excel" placement="top">
                <el-button
                  size="medium"
                  type="primary"
                  icon="el-icon-download"
                  circle
                  @click="getExcel('all')"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column
            align="right"
            width="300">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""
                @keyup.enter.native="searchHandler"/>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          :hide-on-single-page="value"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "AdminAllPack",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        currentPage: 1,       // 
        total: 21,            // 
        pageSize: 10,         // 
        search: '',
        tableData: [{
          id: '12987122',
          org: '',
          perName: 'user1',
          perTel: '12345678900',
          perAddr: '',
          addr: '',
          code: '1-1-16',
          contName: '',
          contTel: '12345678910',
          status: '',
          start: '2020-12-28 10:24:00',
          end: '',
          pick: ''
        }],
        filters: [],
        orgFilter: '',
        statusFilter: 2
      }
    },
    methods: {
		  // 
      handleSizeChange(val) {
        console.log(` ${val} `)
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`)
        this.getPacks(this.orgFilter)
      },
      // 
      searchHandler() {
        this.$message({
          showClose: true,
          type: 'info',
          message: ''
        })
      },
      // 
      handleFilter(filters) {
        if (filters.org !== undefined) {
          this.orgFilter = filters.org
          console.log("org: " + this.orgFilter)
        }
        if (filters.status !== undefined) {
          this.statusFilter = filters.status
          console.log("status: " + this.statusFilter)
        }
        this.getPacks(this.orgFilter, this.statusFilter)
      },
      // 
      getPacks(org, status) {
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        let jsonParam = {
          "currentPage" : this.currentPage,
          "pageSize" : this.pageSize,
          "token" : token,
          "org" : org,
          "status" : status
        }
        param.append('json', JSON.stringify(jsonParam))
        const _this = this
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/pack/getAdminPacksByPage',
          data: param
        })
          .then(function (response) {
            console.log(response.data)
            if (response.data.fail === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
              _this.$router.push('/loginAndRegister')
            } else {
              _this.total = response.data.pack_result.total
              _this.tableData = response.data.pack_result.list
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      indexMethod(index) {
        return (this.currentPage - 1) * this.pageSize + index + 1
      },
      // 
      setFilters() {
        const _this = this
        let card = sessionStorage.getItem("card")
        if (card === '2101') {
          _this.filters = [{ text: '', value: '' }
            , { text: '', value: '' }
            , { text: '', value: '' }]
        } else if (card === '2102') {
          _this.filters = [{ text: '', value: '' }
            , { text: '', value: '' }
            , { text: '', value: '' }]
        } else {
          _this.filters = [{ text: '', value: '' }
            , { text: 'EMS', value: 'EMS' }]
        }
      },
      //  Excel 
      getExcel(type) {
        const _this = this
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        param.append('token', token)
        param.append('type', type)
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/excel',
          data: param,
          responseType: 'blob'
        })
          .then(function (response) {
            console.log(response.data)
            let name = sessionStorage.getItem('name')
            let time = _this.getCurrentTime()
            let fileName = name + "-" + time + "-" + type + ".xlsx"
            _this.download(response.data, fileName)
          })
      },
      //  blob 
      download(data, filename) {
        //var blob = new Blob([data], {type: 'application/vnd.ms-excel'})blob
        if (typeof window.chrome !== 'undefined') {
          // Chrome version
          let link = document.createElement('a');
          link.href = window.URL.createObjectURL(data);
          link.download = filename;
          link.click();
        } else if (typeof window.navigator.msSaveBlob !== 'undefined') {
          // IE version
          let blob = new Blob([data], {type: 'application/force-download'});
          window.navigator.msSaveBlob(blob, filename);
        } else {
          // Firefox version
          let file = new File([data], filename, {type: 'application/force-download'});
          window.open(URL.createObjectURL(file));
        }
      },
      //  yyyy-MM-dd HH:MM:SS
      getCurrentTime() {
        let date = new Date()                             // 
        let month = this.zeroFill(date.getMonth() + 1)  // 
        let day = this.zeroFill(date.getDate())            // 
        let hour = this.zeroFill(date.getHours())          // 
        let minute = this.zeroFill(date.getMinutes())      // 
        let second = this.zeroFill(date.getSeconds())      // 
        // 
        let curTime = date.getFullYear() + month + day + hour + minute + second
        return curTime
      },
      // 
      zeroFill(i){
        if (i >= 0 && i <= 9) {
          return "0" + i
        } else {
          return i
        }
      }
    },
    created() {
      this.setFilters()
      this.getPacks("", 2)
      // const _this = this
      // // 
      // window.setInterval(() => {
      //   setTimeout(_this.getPacks(), 0);
      // }, 3000);
    },
    mounted() {
      this.setFilters()
    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
    font-weight: bold;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



