<template>
	<div>
    <div class="block">
      <div>
        <el-table
          ref="filterTable"
          :data="tableData"
          @filter-change="handleFilter"
          stripe
          style="width: 100%"
          height="750">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label=": ">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.org }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perAddr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.code }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.start }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.end }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.pick }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column
            type="index"
            :index="indexMethod"
            width="100">
          </el-table-column>
          <el-table-column
            type="selection"
            width="70">
          </el-table-column>
          <el-table-column
            label=""
            prop="id"
            width="250">
          </el-table-column>
          <el-table-column
            label=""
            prop="org"
            width="200"
            :filters="[{ text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: 'EMS', value: 'EMS' }]"
            column-key="org"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.org === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.org }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="addr"
            width="150"
            :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]"
            column-key="addr"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.addr === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.addr }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="code"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="status"
            width="150"
            :filters="[{ text: '', value: 0 }, { text: '', value: 1 }, { text: '', value: -1 }]"
            column-key="status"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.status === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.status }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column width="50">
            <template slot="header" slot-scope="scope">
              <!--  -->
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="medium"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteSelection"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column
            align="right"
            width="300">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""
                @keyup.enter.native="searchHandler"/>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          :hide-on-single-page="value"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "UserAllPack",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        currentPage: 1,       // 
        total: 21,            // 
        pageSize: 10,         // 
        search: '',
        tableData: [{
          id: '12987122',
          org: '',
          perName: 'user1',
          perTel: '12345678900',
          perAddr: '',
          addr: '',
          code: '1-1-16',
          contName: '',
          contTel: '12345678910',
          status: '',
          start: '2020-12-28 10:24:00',
          end: '',
          pick: ''
        }],
        orgFilter: '',
        addrFilter: '',
        statusFilter: 2
      }
    },
    methods: {
		  // 
      handleSizeChange(val) {
        console.log(` ${val} `)
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`)
        this.getPacks(this.orgFilter, this.addrFilter)
      },
      // 
      handleDelete(ids) {
        const _this = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = new URLSearchParams()
          param.append('ids', ids)
          let token = sessionStorage.getItem('token')
          param.append('token', token)
          _this.$axios({
            method: 'delete',
            url: _this.baseUrl + '/pack/deletePacks',
            data: param
          })
            .then(function (response) {
              console.log(response.data)
              if (response.data === 'please login to operate') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
                _this.$router.push('/loginAndRegister')
              } else if (response.data === 'do fail') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
              } else if (response.data === 'do success') {
                _this.$message({
                  showClose: true,
                  type: 'success',
                  message: '!'
                })
                let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                _this.$router.push(NewPage)
                _this.$router.go(-1)
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$notify.error({
                showClose: true,
                title: '',
                message: ''
              })
            })
        }).catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
      },
      // 
      searchHandler() {
        this.$message({
          showClose: true,
          type: 'info',
          message: ''
        })
      },
      // 
      handleFilter(filters) {
        if (filters.addr !== undefined) {
          this.addrFilter = filters.addr
          console.log("addr: " + this.addrFilter)
        }
        if (filters.org !== undefined) {
          this.orgFilter = filters.org
          console.log("org: " + this.orgFilter)
        }
        if (filters.status !== undefined) {
          this.statusFilter = filters.status
          console.log("status: " + this.statusFilter)
        }
        this.getPacks(this.orgFilter, this.addrFilter, this.statusFilter)
      },
      // 
      getPacks(org, addr, status) {
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        let jsonParam = {
          "currentPage" : this.currentPage,
          "pageSize" : this.pageSize,
          "token" : token,
          "org" : org,
          "addr" : addr,
          "status" : status
        }
        param.append('json', JSON.stringify(jsonParam))
        const _this = this
        console.log("")
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/pack/getUserPackByPage',
          data: param
        })
          .then(function (response) {
            console.log("")
            console.log(response.data)
            if (response.data.fail === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
              _this.$router.push('/loginAndRegister')
            } else {
              _this.total = response.data.pack_result.total
              _this.tableData = response.data.pack_result.list
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      indexMethod(index) {
        return (this.currentPage - 1) * this.pageSize + index + 1
      },
      // 
      deleteSelection() {
        const _this = this
        if (this.$refs.filterTable.selection.length === 0) {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        } else {
          let ids = ''
          for (let i = 0; i < _this.$refs.filterTable.selection.length; i++) {
            ids += _this.$refs.filterTable.selection[i].id + ','
          }
          this.handleDelete(ids)
        }
      }
    },
    created() {
		  this.getPacks("", "", 2)
    },
    mounted() {

    },
    updated() {

    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
    font-weight: bold;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



