<template>
	<div>
    <div class="block">
      <div>
        <el-table
          ref="filterTable"
          :data="tableData"
          @filter-change="handleFilter"
          stripe
          style="width: 100%"
          height="750">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label=": ">
                  <span>{{ props.row.from_name }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.from_tel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.from_addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.to_name }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.to_tel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.to_addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.price }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.org }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.dt }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column
            type="index"
            :index="indexMethod"
            width="50">
          </el-table-column>
          <el-table-column
            type="selection"
            width="70">
          </el-table-column>
          <el-table-column
            label=""
            prop="to_name"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="price"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="id"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="org"
            width="200"
            :filters="[{ text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: 'EMS', value: 'EMS' }]"
            column-key="org"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.org === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.org }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="status"
            width="100"
            :filters="[{ text: '', value: '' }
              , { text: '', value: '' }
              , { text: '', value: '' }
              , { text: '', value: '' }]"
            column-key="status"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.status === '' ? 'primary' : 'success'"
                disable-transitions>{{ scope.row.status }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="dt"
            width="250">
          </el-table-column>
          <el-table-column width="50">
            <template slot-scope="scope">
              <!--  -->
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="medium"
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="handlePay(scope.$index, scope.row)"></el-button>
              </el-tooltip>
            </template>
            <template slot="header" slot-scope="scope">
              <!--  -->
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="medium"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="cancelSelection"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column
            align="right"
            width="300">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          :hide-on-single-page="value"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "UserSendList",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        currentPage: 1,       // 
        total: 21,            // 
        pageSize: 10,         // 
        search: '',
        tableData: [{
          from_name: '',
          from_tel: '13305193691',
          from_addr: '',
          to_name: '',
          to_tel: '17751773079',
          to_addr: '',
          price: 10,
          id: '75422907315890',
          org: '',
          status: '',
          dt: '2021-01-05 16:45:00',
        }],
        orgFilter: '',
        statusFilter: ''
      }
    },
    methods: {
		  // 
      handleSizeChange(val) {
        console.log(` ${val} `)
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`)
        this.getPacks(this.orgFilter, this.statusFilter)
      },
      // 
      handlePay(index, row) {
        console.log(index, row)
        const _this = this
        if (_this.tableData[index].status === '') {
          let msg = '' + _this.tableData[index].price + ', ?'
          _this.$confirm(msg, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            let param = new URLSearchParams()
            let token = sessionStorage.getItem("token")
            param.append('id', _this.tableData[index].id)
            param.append('token', token)
            _this.$axios({
              method: 'put',
              url: _this.baseUrl + '/send/pay',
              data: param
            })
              .then(function (response) {
                console.log(response.data)
                if (response.data === 'please login to operate') {
                  _this.$notify({
                    showClose: true,
                    title: '',
                    message: '',
                    type: 'warning'
                  })
                  _this.$router.push('/loginAndRegister')
                } else if (response.data === 'do fail') {
                  _this.$notify({
                    showClose: true,
                    title: '',
                    message: '',
                    type: 'warning'
                  })
                } else {
                  _this.$message({
                    showClose: true,
                    type: 'success',
                    message: '!'
                  })
                  let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                  _this.$router.push(NewPage)
                  _this.$router.go(-1)
                }
              })
              .catch(function (error) {
                console.log(error)
                _this.$notify.error({
                  showClose: true,
                  title: '',
                  message: ''
                })
              })
          }).catch(() => {
            _this.$message({
              showClose: true,
              type: 'info',
              message: ''
            })
          })
        } else if (_this.tableData[index].status === '' || _this.tableData[index].status === '') {
          _this.$message({
            showClose: true,
            message: '!',
            type: 'warning'
          })
        } else if (_this.tableData[index].status === '') {
          _this.$message({
            showClose: true,
            message: '!',
            type: 'warning'
          })
        }
      },
      // 
      handleCancel(ids, count) {
        const _this = this
        if (count > 0) {
          _this.$notify.error({
            showClose: true,
            title: '',
            message: ' ' + count + ' '
          })
        }
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = new URLSearchParams()
          let token = sessionStorage.getItem("token")
          param.append('ids', ids)
          param.append('token', token)
          _this.$axios({
            method: 'delete',
            url: _this.baseUrl + '/send/cancel',
            data: param
          })
            .then(function (response) {
              console.log(response.data)
              if (response.data === 'please login to operate') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
                _this.$router.push('/loginAndRegister')
              } else if (response.data === 'do fail') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
              } else {
                _this.$message({
                  showClose: true,
                  type: 'success',
                  message: '!'
                })
                let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                _this.$router.push(NewPage)
                _this.$router.go(-1)
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$notify.error({
                showClose: true,
                title: '',
                message: ''
              })
            })
        }).catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
      },
      // 
      handleFilter(filters) {
        if (filters.org !== undefined) {
          this.orgFilter = filters.org
          console.log("org: " + this.orgFilter)
        }
        if (filters.status !== undefined) {
          this.statusFilter = filters.status
          console.log("status: " + this.statusFilter)
        }
        this.getPacks(this.orgFilter, this.statusFilter)
      },
      // 
      getPacks(org, status) {
        let param = new URLSearchParams()
        let token = sessionStorage.getItem("token")
        let jsonParam = {
          "currentPage" : this.currentPage,
          "pageSize" : this.pageSize,
          "token" : token,
          "org" : org,
          "status" : status
        }
        param.append('json', JSON.stringify(jsonParam))
        const _this = this
        console.log("")
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/send/getSendByUser',
          data: param
        })
          .then(function (response) {
            console.log("")
            console.log(response.data)
            if (response.data.fail === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
              _this.$router.push('/loginAndRegister')
            } else {
              _this.total = response.data.result.total
              _this.tableData = response.data.result.list
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      // 
      indexMethod(index) {
        return (this.currentPage - 1) * this.pageSize + index + 1
      },
      // 
      cancelSelection() {
        const _this = this
        if (this.$refs.filterTable.selection.length === 0) {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        } else {
          let ids = ''
          let count = 0
          for (let i = 0; i < _this.$refs.filterTable.selection.length; i++) {
            ids += _this.$refs.filterTable.selection[i].id + ','
            ids += _this.$refs.filterTable.selection[i].id + ','
            if (_this.$refs.filterTable.selection[i].status === '') {
              count++
            }
          }
          this.handleCancel(ids, count)
        }
      }
    },
    created() {
		  this.getPacks("", "")
    },
    mounted() {

    },
    updated() {

    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
    font-weight: bold;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



